<script>
    import foo from '@/components/foo.vue'
    export default {
        components: {
            foo
        },
        mounted: function () {
            this.token = localStorage.token

            this.route = this.$route.name.toLowerCase()

            this.getInfo()
            this.getAgendas()
            this.getLabs()
        },

        data(){
            return {
                token: '',

                content: {
                    agendas: [],
                    user: [
                        {
                            name: 'Sem nome'
                        }
                    ],
                    labs: [],
                }, 

                route: 'home'
            }
        },

        methods: {
            /* exit */
            exit: function() {
                localStorage.token = ''
                cookieStore.set('token', '*')
                document.location.replace('/login')
            },



            /* requests functions */
            getInfo: async function() {
                const formData = new FormData()
                formData.append('token', this.token)

                let content = await this.$http.post('/info-user', formData, {
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
                })
                if (content.data.status == false) this.exit();

                this.content.user = content.data.content
            },            

            getAgendas: async function() {
                const formData = new FormData()
                formData.append('token', this.token)

                let content = await this.$http.post('/agendas', formData, {
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
                })
                
                this.content.agendas = content.data
            }, 

            getLabs: async function() {
                const formData = new FormData()
                formData.append('token', this.token)

                let content = await this.$http.post('/labs', formData, {
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
                })
                
                this.content.labs = content.data.content
            },            

            /* functions basic */
            toggleBar: function () {
                let open = document.querySelector('.open')
                open.classList.toggle('closebar')

                
                let lbar = document.querySelector('.lbar')
                lbar.classList.toggle('close-lbar')
                
            },

            alterRoute: function () {
                this.route = this.$route.name.toLowerCase()
            }
        }
    }
</script>

