<template>
    <div class="calendar-main">
        <Barid 
            title="Calendario">
        </Barid>

        <div class="options">
          <div class="init-opts"></div>
          <div class="new-event">
            <button @click="openModal">Novo Evento</button>
          </div>

          <div class="modal-add" v-if="modal">
            <div class="modal-in-add">
              <div class="header-md">
                <div class="title">Agendar horario</div>
                <div class="close" @click="closeModal">x</div>
              </div>
              
              <div class="modal-select">
                <div class="t-sel">selecione o lab</div>
                <div class="input-field col s12 m6">
                  <select class="icons" v-model="lab">
                    <option v-for="c, k in content.labs" :key=k data-icon=""
                    :value='c.id' class="left">{{c.name}}</option>
                  </select>
                </div>
              </div>

              <div class="modal-select  m-align">
                <div class="title">Data</div>
                <input type="date" v-model='atual_data'>
              </div>

              <div class="modal-select">
                <div class="horarios">
                  <div class="title">Horarios</div>
                  <div class="turns">

                    <div class="am">
                        <div class="title">Manh</div>

                      <div class="hour" v-for="c in 4" :key="c">
                        <input 
                            name="hour" 
                            :disabled="horarios_oculpados.indexOf(String(c)) != -1" 
                            :value="c" 
                            v-model="horarios" 
                            type="checkbox">

                        <div class="hora">{{7 + c}}:00</div>
                      </div>
                    </div>

                    <div class="pm">
                      <div class="title">Tarde</div>
                      <div class="hour" v-for="c in 4" :key="c">
                        <input 
                            name="hour" 
                            :disabled="horarios_oculpados.indexOf(String(c + 4)) != -1" 
                            :value="4 + c" 
                            v-model="horarios" 
                            type="checkbox">

                        <div class="hora">0{{c}}:00</div>
                      </div>
                      
                    </div>
                  </div>
                </div>
              </div>

              <div class="save-bar">
                <button @click="salvar_data">Salvar</button>
              </div>
            </div>
          </div>
        </div>


        <vc-calendar
            class="custom-calendar"
            :masks="masks"
            :attributes="content.agendas"
            :is-dark="false"
        >
    
        <template v-slot:day-content="{ day, attributes }">
            <div @click="dayClicked(day)" class="flex flex-col h-full z-10 overflow-hidden">
                <span class="day-label text-sm text-gray-900">{{ day.day }}</span>
                <div class="flex-grow overflow-y-auto overflow-x-auto">
                    <p
                        v-for="attr in attributes"
                        :key="attr.key"
                        class="text-xs leading-tight rounded-sm p-1 mt-0 mb-1"
                        :class="attr.customData.class">

                        {{ attr.customData.title }} 
                        
                        </p>
                </div>
            </div>
        </template>
        </vc-calendar>
    </div>
</template>

<script>
    import M from 'materialize-css';
    import Barid from "@/components/Barid.vue"
    export default {
    name: 'Calendar',

    props: {
        token: {
            type: String,
            default(){
                return ''
            }
        },

      content: {
        type: Object,
        default(){
          return {
            user: [],
            agendas: []
          }
        }
      }
    },

    components: {
        Barid
    },

    mounted: function () {
    },


    data(){
    return {
      modal: false,

      
      atual_data: '',
      horarios: [],
      lab: 1,
      horarios_oculpados: [],


      masks: {
        weekdays: 'WWW',
      },

    };
    },


    watch: {
        atual_data: function () {
          this.horarios = []
            this.at_ocupados()
        },
        lab: function () {
          this.horarios = []
            this.at_ocupados()
        }
    },


    methods: {
      openModal: function () {
        this.at_ocupados()
        this.modal = true

        setTimeout(() => {
            
            var elems = document.querySelectorAll('select');
            M.FormSelect.init(elems);
        }, 10);

      },
      at_ocupados: function () {
            this.horarios_oculpados = []
            
            let ymd = this.atual_data.split('-')

            this.content.agendas.forEach(e => {
                let datas = new Date(e.dates)
                if (e.customData.lab == this.lab){
                    if (String(datas.getFullYear()) == ymd[0]) {
                        if (datas.getMonth()+1 == ymd[1]){
                            if (datas.getDate() == ymd[2]){
                                this.horarios_oculpados.push(e.customData.horarios)
                            }
                        }
                    }
                }
            })
        },
      
      closeModal: function () {
        this.modal = false
        this.at_ocupados()
      },


      dayClicked: function(data) {
        let conf = data.date.toLocaleString().split(' ')[0].split('/')
        this.atual_data = `${conf[2]}-${conf[1]}-${conf[0]}` 
        this.openModal()
      },

      salvar_data: async function () {
        let d = this.atual_data.split('-')
        let date =new Date(d[0], d[1] - 1, d[2])

        if (this.horarios.length == 0) {
            M.toast({html: 'Erro! selecione um horario!', classes: 'sleep-toast'})
          return;
        }

        let dates = date.toString()


        const formData = new FormData()
        formData.append('token', this.token)
        formData.append('data', dates)
        formData.append('lab', this.lab)
        formData.append('horarios', JSON.stringify(this.horarios))
        

        M.toast({html: 'criando...', classes: 'sleep-toast'})
        this.at_ocupados()
        this.closeModal()

        let content = await this.$http.post('/new-agenda', formData, {
            headers:{
                'Content-Type': 'multipart/form-data'
            }
        })

        if (content.data.status) {
                M.toast({html: 'criado com sucesso!', classes: 'sucess-toast'})
                this.$emit('updateAgendas')
        }else {
            M.toast({html: 'Erro! impossivel criar!', classes: 'erro-toast'})
        }
        
        this.horarios = []
        this.closeModal()
      }
      
    }
  }
</script>
<style>
    .calendar-main {
      width: 100%;
    }

    .custom-calendar.vc-container {
      border-radius: 0;
      width: 100%;
      padding: 0px;
    }

    .vc-header {
        background-color: #f1f5f8;
        padding: 10px 0 !important;
    }


    .vc-weeks {
        padding: 0px !important;
        margin: 0px;
    }

    .vc-weekday {
        background-color: #f1f5f8a9;
        border-bottom: 1px solid rgba(128, 128, 128, 0.281);
        border-top: 1px solid rgba(128, 128, 128, 0.281);
        padding: 5px 0;
    }


    .vc-container .vc-day {
        min-height: 80px !important;
        padding: 5px;
        cursor: pointer;
    }

    .vc-day:hover {
        box-shadow: 0px 0px 13px rgba(0, 0, 0, 0.144);
    }

    .vc-day:not(.on-bottom) {
        border-bottom: 1px solid rgba(128, 128, 128, 0.281);
    }
    
    .vc-day:not(.on-right) {
        border-right: 1px solid rgba(128, 128, 128, 0.281);
    }

    .text-xs {
        border: 1px solid rgba(128, 128, 128, 0.39);
        margin-bottom: 5px;
        border-radius: 3px;
        padding: 5px;
        color: white;
        background: grey;
        display: flex;
    }

    /* flex */
    .flex  {
        height: 100%;
    }

    /* options */

    .options {
      display: flex;
      align-items: center;
      padding: 10px 0px 20px 0px;
      justify-content: space-between;
    }


    .header-md {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%; 
      padding-bottom: 20px;
    }

    .header-md .close {
      font-weight: bold;
      height: 20px;
      width: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: rgba(128, 128, 128, 0.315);
      border-radius: 100%;
      cursor: pointer;
    }
    
    .options .new-event {
      padding: 0px 10px;
    }

    .new-event button {
      background: white;
      color: black;
      height: 30px;
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      border: 1px solid grey;
    }

    button:hover {
      background: initial;
      cursor: pointer;
    }

    button:active {
      background: white !important;
      transform: scale(.95);
      color: black;
    }

    button:focus {
      background: rgba(255, 255, 255, 0.425) !important;
      color: black !important;
    }

    .modal-add {
      position: absolute;
      background: rgba(0, 0, 0, 0.247);
      width: 100%;
      height: 100%;
      left: 0px;
      top: 0px;
      z-index: 2;
      display: flex;
      justify-content: center;
      padding: 20px;
    }

    .modal-in-add {
      background: white;
      width: 100%;
      max-width: 400px;
      max-height: 450px;
      padding: 20px;
      display: flex;
      align-items: center;
      flex-direction: column;
      overflow: auto;
    }

    .modal-in-add .title {
      font-size: 20px;
      border-bottom: 1px solid rgba(128, 128, 128, 0.452);
      width: 100%;
    }


    .modal-select .t-sel {
      width: 100%;
      text-transform: capitalize;
      font-size: 17px;
    }
    .modal-select {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid rgba(128, 128, 128, 0.247);
      width: 100%;
      padding: 5px 5px;
    }

    .modal-select select {
      border: 1px solid;
      cursor: pointer;
    }


    .save-bar {
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
    }

    .save-bar button {
      background: rgb(53, 212, 5);
      color: rgb(255, 255, 255);
      height: 30px;
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      border: 1px solid rgba(128, 128, 128, 0.438);
    }

    .horarios {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
    }

    .horarios .title {
      border: none;
    }


    .modal-add .title {
      font-size: 17px;
    }

    input[type='checkbox'] {
      display: initial !important;
      position: relative !important;
      opacity: 1 !important; 
      pointer-events: initial !important;
      cursor: pointer !important;
    }


    .hour {
      display: flex;
    }

    .turns {
      width: 100%;
      display: flex;
      justify-content: space-around;
    }
    .turns input {
      margin: 0px 5px 0px 0px;
    }


    .m-align {
      align-items: center;
    }

    .m-align .title {
      border: none;
    }
</style>