<template>
    <div class="new-main">
        <Barid title="New Lab"></Barid>

        <div class="new-content">
          <div class="image">
            <img v-if="type == 1" src="../assets/icons/informatica.png">
            <img v-else src="../assets/icons/livros.png">
          </div>
          <div class="opt-n">
            <div class="opt-sep">
              <div class="input-field col s12">
                <select v-model="type">
                  <option value="" disabled selected>Selecione o tipo</option>
                  <option value="1">Informatica</option>
                  <option value="2">Outro</option>
                </select>
              </div>
            </div>

            <div class="opt-sep">
                <div class="input-field col s6">
                    <div class="input-field col s6">
                        <input maxlength="30" id="last_name" type="text" class="validate" v-model="name">
                        <label for="last_name">insira um nome (obrigatorio!)</label>
                    </div>
                </div>
            </div>
          </div>
        </div>

        <div class="save-area">
            <button @click='CreateLab'>Cadastrar</button>
        </div>
    </div>
</template>

<script>
    // @ is an alias to /src
    import Barid from '@/components/Barid.vue'
    import M from 'materialize-css';



    export default {
      name: 'new',

      props: {
        token: {
            type: String,
            default(){
                return ''
            }
        },

      content: {
        type: Object,
        default(){
          return {
            user: [],
            agendas: [],
            labs: []
          }
        }
      }
    },

      data: ()=> {
        return {
            type: 1,
            name: ''
        }
      },

      mounted(){
          var elems = document.querySelectorAll('select');
          M.FormSelect.init(elems);
      },
      methods: {
        
        CreateLab: async function() {
                const formData = new FormData()
                formData.append('token', this.token)
                formData.append('name', this.name)
                formData.append('type', this.type)



                this.name = ''
                M.toast({html: 'Criando...', classes: 'sleep-toast'})

                let content = await this.$http.post('/new-lab', formData, {
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
                })

                if (content.data.status) {
                    M.toast({html: 'Criado com sucesso!', classes: 'sucess-toast'})
                    this.$emit('updateLabs')

                }else {
                    M.toast({html: 'Erro! Revise os dados!', classes: 'erro-toast'})
                }

            },  
      },

      components: {
        Barid
      }
    }
</script>


<style>

.sleep-toast {
    color: rgb(224, 203, 8) !important;
}

.sucess-toast {
    color: rgb(6, 199, 6) !important;
}

.erro-toast {
    color: rgb(209, 1, 1) !important;
}

</style>

<style scoped>



.new-main {
  width: 100%;
  display: flex;
  flex-direction: column;
}


.new-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.opt-n {
    width:100%;

    max-width: 500px;
}

.new-content .image {
  width: 200px;
  height: 200px;
  box-shadow: 0px 0px 13px rgba(0, 0, 0, 0.24);
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
}

.image img {
  width: 50%;
}

.input-field.col label {
    left: 0px !important;
}
.opt-sep {
    width: 100%;
    margin-bottom: 30px;
    text-align: right !important;
}

.save-area {  display: flex;
  justify-content: center;
  align-items: center;

  padding: 20px;
}

.save-area button {
    width: 100%;
    max-width: 300px;
    height: 40px;
    font-weight: bold;
    background: white;
    border-radius: 10px 0px;
    border: 1px solid rgba(128, 128, 128, 0.651);
    color: green;
    font-size: 17px;

}

</style>