<template>
    <div class="heade-not">
        <div class="title">
            {{u_title}}
        </div>
        <div class="content-d">|</div>
    </div>
</template>

<script>
    export default {
      name: 'HelloWorld',
      props: {
        title: String
      },

       mounted: function() {
            let i = 0
            let stop = false 
            setInterval(() => {
                if (!stop){
                this.u_title += this.title[i]
                i++;
                if (i >= this.title.length) {
                    i = 0;
                    stop = true
                    setTimeout(() => {
                        this.u_title = ''
                        stop = false
                    }, 7000);
                }}
            }, 200);
       },

      data: function() {
        return {
            u_title: ''
        }
      }
    }
</script>

<style scoped>
    .heade-not {
        background:  white;
        border: 1px solid rgba(128, 128, 128, 0.336);
        display: flex;
        align-items: center;
        align-content: center;
        margin: 0px 0px 20px 0px;
        font-size: 30px;
        color: rgb(0, 8, 46);
        padding: 40px 30px;
    }

    .heade-not .content-d {
        animation: pisca 700ms linear infinite; 
        margin: 0px 5px;
    }

    @keyframes pisca {
        100% {
            opacity: 0;
        }
    }

</style>