import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'
import Calendar from '../views/Calendar.vue'
import Manage from '../views/Manage.vue'
import New from '../views/N.vue'
import Hours from '../views/Hours.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/calendar',
    name: 'calendar',
    component: Calendar
  },

  {
    path: '/manage',
    name: 'manage',
    component: Manage
  },
  {
    path: '/new-lab',
    name: 'new',
    component: New
  },
  {
    path: '/hours',
    name: 'hours',
    component: Hours
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
