<template>
    <div class="horarios-main">
        <Barid title="Meus Horrios"></Barid>
        
        <div class="content-h">
            <div class="horario"
                v-for="c, k in content.agendas"
                :key=k
                :style="{'background': RandomColor(k), 'color': RandomColor(k+1)}"
                >

                <div class="lab">Lab - {{c.customData.lab}}</div>
                <div class="hour">H - {{convertHour(c.customData.horarios)}}:00 AM</div>
                <div class="hour">Data - {{convertDate(c.dates)}}</div>
                <button @click='deleteHour(c.key)'>
                    <img src="../assets/icons/delete.png" alt="">
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    // @ is an alias to /src
    import Barid from '@/components/Barid.vue';
    import M from 'materialize-css';


    export default {
      name: 'Hours',


      props: {
        token: {
            type: String,
            default(){
                return ''
            }
        },

      content: {
        type: Object,
        default(){
          return {
            user: [],
            agendas: []
          }
        }
      }
    },

      data: ()=> {
        return {
        }
      },

      mounted(){
      },

      methods: {
        deleteHour: async function(id){
            const formData = new FormData()
            formData.append('token', this.token)
            formData.append('id', id)



            this.name = ''
            M.toast({html: 'Apagando...', classes: 'sleep-toast'})

            let content = await this.$http.post('/delete-agenda', formData, {
            headers:{
                'Content-Type': 'multipart/form-data'
            }
            })

            if (content.data.status) {
                M.toast({html: 'Apagado com sucesso!', classes: 'sucess-toast'})
                this.$emit('updateAgendas')

            }else {
                M.toast({html: 'Erro! impossivel apagar!', classes: 'erro-toast'})
            }
        },

        convertDate: function(args) {
            let data = new Date(args)
            
            return `${data.getFullYear()}/${data.getMonth()}/${data.getDate()}`
        },

        convertHour: function(args) {
            if (Number(args) > 4){
                return Number(args) - 4
            }                
            return Number(args) + 7
        },

        
        RandomColor: function(k) {
            /*let r = Math.round(Math.random() * 255)
            let g = Math.round(Math.random() * 255)
            let b = Math.round(Math.random() * 255)

            return `rgb(${r}, ${g}, ${b})`*/
            if (k % 2 == 0) {
                return 'white'
            }else return 'black'
        },


      },

      components: {
        Barid
      }
    }
</script>

<style scoped>
    .horarios-main {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .horario {
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-shadow: 1px 2px 13px rgba(0, 0, 0, 0);
        padding: 0px 0px 0px 10px;
        height: 40px;
        border-radius: 1px;
        background: rgb(26, 128, 1);
        font-weight: bold;
        color: white;
        overflow: hidden;
        user-select: none;
        margin-bottom: 10px;
    }

    .horario button {
        height: 100%;
        width: 50px;
        background: white;
        border: none;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .horario button img {
        width: 20px;
    }

    button:active {
        transform: scale(1);
        background: white !important;
    }

    button:focus {
        transform: scale(1);
        background: white !important;
    }

</style>