<template>
    <div class="magane-main">
        <Barid title="Manage"></Barid>

        <div class="search-bar">
            <div class="row">
                <div class="input-field col s6">
                <input v-model="search" id="first_name2" type="text" maxlength="40" class="validate">
                <label for="first_name2">Serch per labs</label>
                </div>
            </div>
        </div>

        <div class="m-head"></div>
        <div class="m-content">
            <template v-for="c, k in content.labs">
                <div class="card-lab" v-if="c.name.indexOf(search) != -1"
                    :key="k">
                    <div class="labimage">
                        <img src="../assets/icons/informatica.png">
                        
                        <template>
                            <div class="lab-title" v-if="c.type==1">Lab. Informatica</div>
                            <div class="lab-title" v-else>Lab. Humanas</div>
                        </template>
                        
                        <div class="name">{{c.name}}</div>
                    </div>

                    <div class="lab-actions">
                        <button @click=deletelab(c.id)>Apagar</button>
                    </div>
                </div>
            </template>
        </div>

        <div v-if="content.labs.length <= 0" class="no-have">
            <div class="ct">Nada por aqui!</div>
        </div>
    </div>
</template>

<script>
import Barid from "@/components/Barid.vue"
import M from 'materialize-css';


export default {
    name: 'manage',

    
    props: {
        token: {
            type: String,
            default(){
                return ''
            }
        },

      content: {
        type: Object,
        default(){
          return {
            user: [],
            agendas: [],
            labs: []
          }
        }
      }
    },

    data: function() {
        return {
            search: ''
        }
    },

    components: {
        Barid
    },

    mounted: () =>{
    },

    methods: {
        deletelab: async function(id){
            const formData = new FormData()
                formData.append('token', this.token)
                formData.append('id', id)


                M.toast({html: 'Apagando...', classes: 'sleep-toast'})

                let content = await this.$http.post('/delete-lab', formData, {
                headers:{
                    'Content-Type': 'multipart/form-data'
                }
            })

            
            if (content.data.status) {
                M.toast({html: 'Criado com sucesso!', classes: 'sucess-toast'})
                this.$emit('updateLabs')

            }else {
                M.toast({html: 'Erro! Revise os dados!', classes: 'erro-toast'})
            }
        }
    }
}  
</script>

<style scoped>

.no-have {
    width: 100%;
    color: grey;
    font-size: 19px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.magane-main {
    width: 100%;
}


.labimage {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
}


.lab-title  {
    font-weight: bold;  
    margin: 5px 0px; 
}

.name {
    color: grey;
    font-size: 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
}

.labimage img {
    width: 70%;
}

.card-lab { 
    background: white;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin: 10px;
    max-width: 200px;
    overflow: hidden;
}

.lab-actions {
    border-top: 1px solid rgba(128, 128, 128, 0.301);
    width: 100%;
    padding: 10px 0px;
    display: flex;
    margin-top: 15px;
}

.m-content {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

button {
    width: 100%;
    max-width: 100px;
    height: 30px;
    background: rgba(238, 6, 6, 0.884);
    border: 1px solid rgba(88, 88, 88, 0.24);
    color: white;
    border-radius: 2px;
    font-weight: bold;
}

button:focus {
    background: red !important;
    color: white !important;
}

</style>